import React from 'react'
import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import theme, { responsive } from '../../../components/ThemeProvider/theme'

import Table from './Table'
import Invest from './Invest'
import Info from './Info'
import Source from '../../../components/Source'

const TypeText = ({ type, currency }) => <Text><Text.Inline fontWeight="bold" color="custom.textBlue">{type}：</Text.Inline>{currency}</Text>

const fundData = [
  {
    th: '基金總類',
    td: '債券型'
  },
  {
    th: '投資地區/範圍',
    td: '全球；海外債券、新興市場債券、 ESG綠色永續投資概念之債券為主'
  },
  {
    th: '風險報酬等級',
    td: 'RR3'
  },
  {
    th: '發行類型*',
    td: (
      <Box>
        <TypeText type="A累積型" currency="新臺幣、美元、人民幣" />
        <TypeText type="B月配型" currency="新臺幣、美元、人民幣、南非幣" />
        <TypeText type="NA類型(後收累積)" currency="新臺幣、美元、人民幣" />
        <TypeText type="NB類型(後收月配)" currency="新臺幣、美元、人民幣、南非幣" />
      </Box>
    )
  },
  {
    th: '經理費*',
    td: '1.70%'
  },
  {
    th: '保管費',
    td: '0.26%'
  },
  {
    th: '買回開始日',
    td: '成立日起90日後'
  },
  {
    th: '買回付款日',
    td: '應自受益人提出買回受益憑證之請求到達之次一營業日起7個營業日內'
  },
]

const links = [
  {
    text: 'D M',
    href: ''
  },
  {
    text: '公開說明書',
    href: ''
  },
  {
    text: '簡式公開說明書',
    href: ''
  },
]

const Fund = () => {
  return (
    <Box pt={theme.headerHeight}>
      <Text.Bold textAlign="center" color="custom.blue" fontSize={responsive('1.5em', '2.5em')}>基金資料</Text.Bold>
      <Table data={fundData} mt="1.75em" mx="auto" width={responsive('auto', '80%')} />
      <Source borderColor="custom.borderBlue">
        資料來源：凱基投信。資料日期：2021/6/30。風險報酬等級為本公司依照投信投顧公會「基金風險報酬等級分類標準」編製，分類為 RR1-RR5 五級，數字越大代表風險越高。此等級分類係基於一般市場狀況反映市場價格波動風險，無法涵蓋所有風險，不宜作為投資唯一依據，投資人仍應注意所投資基金個別的風險。 *完整發行類型及費用資訊依基金公開說明書為準。
      </Source>
      <Flex alignItems="center" flexDirection={responsive('column', 'row')}>
        {links.map(({ text, href }, k) => (
          <Box width={responsive('100%', 1 / 3)} mt="1em" mx={responsive(0, '0.5em')} key={k}>
            <Button borderRadius="1.5em" width="100%" py="1rem" href={href} fontSize={responsive('1em', '1.25em')}>{text}</Button>
          </Box>
        ))}
      </Flex>
      <Invest />
      <Info />
    </Box>
  )
}

export default Fund
