import React from 'react';

import Box from '../../../components/Box'
import { responsive } from '../../../components/ThemeProvider/theme'

const Table = ({ data, ...props }) => {
  return (
    <Box
      as="table"
      borderRadius={responsive('3rem', '5rem')}
      overflow="hidden"
      fontSize={responsive('0.75em', '1.25em')}
      {...props}
    >
      <tbody>
        {data.map(({ th, td }, i) => (
          <Box as="tr" key={i}>
            <Box
              width="29%"
              as="th"
              px="0.5em"
              color="white"
              textAlign="center"
              whiteSpace="pre-wrap"
              bg="custom.lightBlue"
            >
              {th}
            </Box>
            <Box
              as="td"
              pr="2.625em"
              color="black"
              whiteSpace="pre-wrap"
              bg="white"
            >
              <Box pl="1rem" py="1rem" borderTop={i && '1px solid'} borderColor="custom.lightBlue">{td}</Box>
            </Box>
          </Box>
        ))}
      </tbody>
    </Box>
  );
};

export default Table;
